.colorButtons_container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  box-sizing: border-box;
  width: 375px;
  height: 320px;
  padding: 10px;
  margin-bottom: 20px;
  overflow-y: scroll;

  &::-webkit-scrollbar {
    width: 8px; 
  }

  &::-webkit-scrollbar-track {
    background: rgb(28, 28, 28); 
    border-radius: 10px;
  }

  &::-webkit-scrollbar-thumb {
    background: #6dbd8b;
    border-radius: 10px;
  }

  &::-webkit-scrollbar-thumb:hover {
    background: #138a5c; 
  }

  button {
    border: 3px solid transparent;
    border-radius: 10px;
    margin: 1px;
    font-size: 15px;
    height: 40px;
    box-sizing: border-box;
    font-family: "Cera Pro", sans-serif;

    &:hover {
      cursor: pointer;
      border-color: #6dbd8b;
    }

    &:active {
      border: 3px solid #00b144;
      color: white;
    }
  }
}

@media (max-width: 1550px) {
  .colorButtons_container {
    width: 475px;
  }
}
@media (max-width: 880px) {
  .colorButtons_container {
    width: 100%;
  }
}

